import { Component } from 'react';
import ReactDOM from 'react-dom';
import { ListGroup, Badge } from 'react-bootstrap';
import "../../bootstrap/css/bootstrap.min.css";
import { Tooltip, OverlayTrigger } from 'react-bootstrap';
import '../../css/App.css';
import TestStepForm from './components/TestStepForm';
import SuccessMessage from './components/SuccessMessage';
import Reports from '../../Reports';
import Dashboard from '../../Dashboard';
import About from '../../Help';
const env = require('../../env.json');

class ContentView extends Component {
    constructor(props) {
        super(props);
        this.props = props;
        this.isStepsDisplayed = props.isStepsDisplayed;
        this.isEachStepDisplayed = false;
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            reusability: {},
            stepData: {}
        }
    }

    fetchTestSteps(testCaseName) {
        var uri = env.server + "project/getteststeps?project_path=" + localStorage.getItem("projectPath") + "&test_name=" + testCaseName + ".json";
        fetch(uri)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: false,
                        error
                    });
                }
            );
    }

    fetchReusability() {
        var projectName = localStorage.getItem("projectPath");
        var uri = env.server + "project/getreusability?project_path=" + projectName;
        fetch(uri)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        reusability: result
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: false,
                        error
                    });
                }
            );
    }

    fetchTestStepData(testStepName) {
        var projectName = localStorage.getItem("projectPath");
        var uri = env.server + "project/gettestStep?project_path=" + projectName + "&test_name=" + localStorage.getItem("testName") + "&step_name=" + testStepName;
        fetch(uri)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        stepData: result
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: false,
                        error
                    });
                }
            );
    }

    componentDidMount() {
        if(this.props.testCaseName != undefined) {
            this.fetchTestSteps(this.props.testCaseName);
            this.fetchReusability();
            this.isStepsDisplayed = true;
        }
    }

    getComponentReusability(componentName) {
        for(let i = 0; i < Object.keys(this.state.reusability).length; i ++) {
            if(Object.keys(this.state.reusability)[i] == componentName)
                return this.state.reusability[componentName];
        }
    }

    renderTooltip = (props, text) => (
        <Tooltip id="button-tooltip" {...props}>
          {text}
        </Tooltip>
    );

    handleTestStepClick(event) {
        this.setState({
            isLoaded: false
        });
        this.setState({reusability: {}});
        this.testStepName = event.target.innerText;
        localStorage.setItem("stepName", this.testStepName);
        this.fetchTestStepData(event.target.innerText);
        this.isStepsDisplayed = false;
        this.isEachStepDisplayed = true;
    }

    loadEachTestSteps() {
        var testSteps = [];
        var count = 0;
        this.state.items.forEach((eachItem) => {
            var eachItemElem = 
            <ListGroup.Item
                as="li"
                className="d-flex justify-content-between align-items-start hover-list-item" style={{cursor: "pointer"}}
                key={++count}
            >
                <div className="ms-2 me-auto">
                    <div className="fw-bold" onClick={(e) => this.handleTestStepClick(e)}>{eachItem}</div>
                </div>
                <OverlayTrigger
                    placement="left"
                    delay={{ show: 250, hide: 400 }}
                    overlay={this.renderTooltip(this.props, "Number of times component is used")}
                >
                    <Badge variant="primary" pill>
                    {
                        this.getComponentReusability(eachItem)
                    }
                    </Badge>
                </OverlayTrigger>
            </ListGroup.Item>;
            testSteps.push(eachItemElem);
        });
        return testSteps;
    }

    componentWillReceiveProps(newProps) {
        this.isStepsDisplayed = newProps.isStepsDisplayed;
        this.fetchTestSteps(newProps.testCaseName);
        this.fetchReusability();
    }

    loadTestSteps() {
        return (<ListGroup as="ol" numbered style={{paddingTop: "7px"}}>
            {
                this.loadEachTestSteps()
            }
        </ListGroup>)
    }

    runTest(testName) {
        var requestBody = {
            "project_path":"aura-api-automation",
            "test_name": testName
        }
        var url = env.server + "tests/run";
        fetch(url, {
            body: JSON.stringify(requestBody),
            headers: {
                "content-type": "application/json"
            },
            method: "post"
        }).then(res => res.json())
        .then(
            (result) => {
                ReactDOM.render(
                    <></>, document.getElementById("message")
                );
                if(result.status == "Pass") 
                    ReactDOM.render(
                        <SuccessMessage showAlert={true} title={localStorage.getItem("testName") + " executed successfully and PASSED"} message={<a href="/Reports">Find your logs here</a>} alertType="success"/>,
                        document.getElementById("message")
                    );
                else if(result.status == "Skip")
                    ReactDOM.render(
                        <SuccessMessage showAlert={true} title={localStorage.getItem("testName") + " was SKIPPED"} message={<a href="/Reports">Find your logs here</a>} alertType="warning"/>,
                        document.getElementById("message")
                    );
                else
                    ReactDOM.render(
                        <SuccessMessage showAlert={true} title={localStorage.getItem("testName") + " executed successfully and FAILED"} message={<a href="/Reports">Find your logs here</a>} alertType="danger"/>,
                        document.getElementById("message")
                    );
            },
            (error) => {
                ReactDOM.render(
                    <></>, document.getElementById("message")
                );
                ReactDOM.render(
                    <SuccessMessage showAlert={true} title={localStorage.getItem("testName") + " executed successfully and FAILED"} message="" alertType="danger"/>,
                    document.getElementById("message")
                );
            }
        );
    }

    subHeader() {
        return (
        <div className="bg-col" style={{height: "4em", backgroundColor: "#255095"}}>
            <div id="message" style={{position: "absolute", zIndex: 2, width: "100%"}} />
            <img src={require("../../images/tc.png")} style={{width: "5%", position: "relative", left: "1%", display: "inline-block"}}/>
            <div className="testCaseNameAnimate" style={{fontSize: "25px", left: "3%", width: "max-content", display: "inline-block"}}>{localStorage.getItem("testName")}</div>
            <OverlayTrigger
                    placement="left"
                    delay={{ show: 250, hide: 400 }}
                    overlay={this.renderTooltip(this.props, "Run Test")}
                >
                <img onClick={() => this.runTest(localStorage.getItem("testName"))} 
                    style={{display: "inline-block", width: "3%", float: "right", position: "relative", right: "2%", top: "23%", cursor: "pointer"}} 
                    src={require('../../images/run.png')} 
                />
            </OverlayTrigger>
        </div>);
    }

    render() {
        const {error, isLoaded, items} = this.state;
        if(error) {
            return <div>error</div>
        } 
        else if(!isLoaded) {
            if(window.location.href.includes("/Home")) {
                return (<div style={{textAlign: "center", height: "inherit"}}>
                            <div id="message" />
                            <img style={{position: "relative", top: "15vh", opacity: "0.5"}} src={require("../../images/logo.png")} alt="bata logo" />
                        </div>)
            } else if(window.location.href.includes("/Reports")) {
                return <Reports />
            } else if(window.location.href.includes("/Dashboard")) {
                return <Dashboard />
            } else if(window.location.href.includes("/Help")) {
                return <About />
            } else {
                return (<div style={{textAlign: "center", height: "inherit"}}>
                            <div id="message" />
                            <img style={{position: "relative", top: "15vh", opacity: "0.5"}} src={require("../../images/logo.png")} alt="bata logo" />
                        </div>)
            }
        } 
        else if(isLoaded) {
            if(this.isStepsDisplayed) {
                var testSteps = [];
                testSteps.push(this.subHeader())
                testSteps.push(this.loadTestSteps());
                return testSteps;
            } else if(this.isEachStepDisplayed) {
                this.isStepsDisplayed = false;
                return <TestStepForm testParam={this.state.stepData} />
            } else {
                return (<div>
                    <img src="../../images/logo.png" alt="bata logo" />
                </div>)
            }
        }
    }
}

export default ContentView;