import ReactDOM from "react-dom";
import { Button, OverlayTrigger, Tooltip } from "react-bootstrap";
import request from "sync-request";
import "../../../bootstrap/css/bootstrap.min.css";
import "../../../css/App.css";
import SuccessMessage from "./SuccessMessage";
const env = require("../../../env.json");

function prettyPrintJSON(e, id) {
    var ugly = document.getElementById(id).value;
    console.log(ugly);
    var obj = JSON.parse(ugly);
    var pretty = JSON.stringify(obj, undefined, 4);
    document.getElementById(id).value = pretty;
}

function handleToExecuteSelect(e) {
    if(e.target.value == "no") {
        document.getElementById("StatusCode").disabled = true;
        document.getElementById("EndPoint").disabled = true;
        document.getElementById("RequestType").disabled = true;
        document.getElementById("Payload").disabled = true;
        document.getElementById("Headers").disabled = true;
        document.getElementById("Store").disabled = true;
        document.getElementById("ReportResponse").disabled = true;
        document.getElementById("ShowPayload").disabled = true;
    } else if(e.target.value == "yes") {
        document.getElementById("StatusCode").disabled = false;
        document.getElementById("EndPoint").disabled = false;
        document.getElementById("RequestType").disabled = false;
        document.getElementById("Payload").disabled = false;
        document.getElementById("Headers").disabled = false;
        document.getElementById("Store").disabled = false;
        document.getElementById("ReportResponse").disabled = false;
        document.getElementById("ShowPayload").disabled = false;
    }
}

function handleFormChange() {
    document.getElementById("saveCancelBlock").style.display = "block";
}

function firstLetterCapital(str) {
    return str.toString()[0].toUpperCase() + str.slice(1);
}

function handleSave() {
    var requestBody = {
        "ToExecute": firstLetterCapital(document.getElementById("ToExecute").value.toString()),
        "SerialNo": parseInt(document.getElementById("SerialNumber").innerText),
        "StatusCode": parseInt(document.getElementById("StatusCode").value),
        "EndPoint": document.getElementById("EndPoint").value,
        "RequestType": document.getElementById("RequestType").value.toUpperCase(),
        "Payload": JSON.parse(document.getElementById("Payload").value),
        "Headers": JSON.parse(document.getElementById("Headers").value),
        "Store": JSON.parse(document.getElementById("Store").value),
        "ReportResponse": (document.getElementById("ReportResponse").value === 'true'),
        "ShowPayload": (document.getElementById("ShowPayload").value === 'true'),
    };
    var url = env.server + "project/" + localStorage.getItem("projectPath") + "/testcase/" + localStorage.getItem("testName") + "/teststep/" + localStorage.getItem("stepName")
    fetch(url, {
        body: JSON.stringify(requestBody),
        headers: {
            "content-type": "application/json"
        },
        method: "post"
    }).then(res => res.json())
    .then(
        (result) => {
            ReactDOM.render(
                <SuccessMessage showAlert={true} title="Saved successfully" message="" alertType="success"/>,
                document.getElementById("messageText")
            );
        },
        (error) => {
            console.log(error);
            ReactDOM.render(
                <SuccessMessage showAlert={true} title="Failed to save :(" message="" alertType="danger"/>,
                document.getElementById("messageText")
            );
        }
    );
}

const renderTooltip = (text) => (
    <Tooltip id="button-tooltip">
      {text}
    </Tooltip>
);

function reportResponseDefault(testParam) {
    if(testParam.ReportResponse === undefined)
        return "false";
    else if(testParam.ReportResponse === false)
        return "false";
    else
        return "true";
}

function showPayloadDefault(testParam) {
    if(testParam.ShowPayload === undefined)
        return "false";
    else if(testParam.ShowPayload === false)
        return "false";
    else
        return "true";
}

function postUI(testParam, setShow) {
    return(
        <div className="form-group" onChange={() => handleFormChange()}>
            <div id="messageText" style={{width: "85.8%", position: "absolute", "zIndex": 2, textAlign: "center"}}></div>
            <div className="bg-col" style={{height: "4em", marginBottom: "1em", backgroundColor: "#255095"}}>
                <div style={{fontSize: "25px", position: "relative", left: "0.5%", width: "max-content"}}>{localStorage.getItem("testName")}</div>
                <div style={{display: "inline-block", position: "relative", left: "0.5%", fontSize: "14px"}}>Serial Number : 
                    <span id="SerialNumber"> {testParam.SerialNo}</span>
                </div>
                <div style={{display: "inline-block", marginLeft: "2px", float: "right", position: "relative", top: "-22%", width: "13%"}} className="form-group col-md-2">
                    <OverlayTrigger
                        placement="left"
                        delay={{ show: 250, hide: 400 }}
                        overlay={renderTooltip("Show payload")}
                    >
                        <select className="form-control" id="ShowPayload" name="ShowPayload" defaultValue={showPayloadDefault(testParam)}>
                            <option defaultValue={undefined}>Show Payload</option>
                            <option value="true">Yes</option>
                            <option value="false">No</option>
                        </select>
                    </OverlayTrigger>
                </div>
                <div style={{display: "inline-block", marginLeft: "2px", float: "right", position: "relative", top: "-22%", width: "13%"}} className="form-group col-md-2">
                    <OverlayTrigger
                            placement="left"
                            delay={{ show: 250, hide: 400 }}
                            overlay={renderTooltip("Report response")}
                        >
                        <select className="form-control" defaultValue={reportResponseDefault(testParam)} id="ReportResponse" name="ReportResponse">
                            <option defaultValue={undefined}>Report Response</option>
                            <option value="true">Yes</option>
                            <option value="false">No</option>
                        </select>
                    </OverlayTrigger>
                </div>
            </div>
            <div style={{marginLeft: "7%"}}>
                <div class="input-group" style={{width: "30%",
            display: "-webkit-inline-box"}}>
                    <div class="input-group-prepend">
                        <span class="input-group-text" style={{backgroundColor: "#255095", color: "aliceblue"}}>Request Type</span>
                    </div>
                    <select style={{width: "50%"}} defaultValue={testParam.RequestType.toLowerCase()} className="form-control" name="RequestType" id="RequestType">
                        <option defaultValue={undefined}>Select</option>
                        <option value="get">GET</option>
                        <option value="post">POST</option>
                        <option value="put">PUT</option>
                        <option value="patch">PATCH</option>
                        <option value="delete">DELETE</option>
                    </select>
                </div>
                <div class="input-group" style={{width: "70%",
            display: "-webkit-inline-box"}}>
                    <div class="input-group-prepend">
                        <span style={{backgroundColor: "#255095", color: "aliceblue"}} class="input-group-text">End point</span>
                    </div>
                    <input style={{width: "80%"}} defaultValue={testParam.EndPoint} class="form-control" type="text" aria-label="With textarea" id="EndPoint" />
                </div>
            </div>
            <div style={{display: "inline-block", width: "40%", position: "relative", left: "7%"}}>
                <div class="input-group" style={{width: "60%", marginBottom: "5%"}}>
                    <div class="input-group-prepend">
                        <span style={{backgroundColor: "#255095", color: "aliceblue"}} class="input-group-text">To Execute</span>
                    </div>
                    <select onChange={(e) => handleToExecuteSelect(e)} defaultValue={testParam.ToExecute.toLowerCase()} className="form-control" id="ToExecute" name="ToExecute">
                        <option defaultValue={undefined}>Select</option>
                        <option value="yes">Yes</option>
                        <option value="no">No</option>
                    </select>
                </div>
                <div class="input-group" style={{width: "60%"}}>
                    <div class="input-group-prepend">
                        <span style={{backgroundColor: "#255095", color: "aliceblue"}} class="input-group-text">Status Code</span>
                    </div>
                    <input type="text" defaultValue={testParam.StatusCode} pattern="[0-9]*" class="form-control" id="StatusCode" placeholder="Enter status code"></input>
                </div>
                <div className="form-group col-md-12" style={{marginTop: "2%"}}>
                    <label>Payload</label>
                    <textarea class="form-control" defaultValue={JSON.stringify(testParam.Payload, undefined, 4)} id="Payload" />
                    <button style={{position: "relative", top: "5px", padding: "3px", fontSize: "12px"}} type="button" class="btn btn-secondary" onClick={(e) => prettyPrintJSON(e, "Payload")}>Pretify</button>
                </div>
            </div>
            <div style={{display: "inline-block", width: "40%", marginLeft: "12%"}}>
                <div className="form-group col-md-12" style={{marginTop: "2%"}}>
                    <label>Headers</label>
                    <textarea defaultValue={JSON.stringify(testParam.Headers, undefined, 4)} class="form-control" id="Headers" />
                    <button style={{position: "relative", top: "5px", padding: "3px", fontSize: "12px"}} type="button" class="btn btn-secondary" onClick={(e) => prettyPrintJSON(e, "Headers")}>Pretify</button>
                </div>
                <div className="form-group col-md-12" style={{marginTop: "2%"}}>
                    <label>Store Value</label>
                    <textarea defaultValue={JSON.stringify(testParam.Store, undefined, 4)} class="form-control" id="Store" />
                    <button style={{position: "relative", top: "5px", padding: "3px", fontSize: "12px"}} type="button" class="btn btn-secondary" onClick={(e) => prettyPrintJSON(e, "Store")}>Pretify</button>
                </div>
            </div>
            <div className="saveCancelAnimate" id="saveCancelBlock" style={{position: "absolute", width: "85%", top: "85%", height: "10%", display: "none"}}>
                <div style={{float: "right", position: "relative", right: "1%", top: "15%"}}>
                    <Button style={{marginRight: "5em"}} title="Save">Cancel</Button>
                    <Button style={{backgroundColor: "#3fd329", color: "black"}} title="Save" onClick={() => handleSave(setShow)}>Save</Button>
                </div>
            </div>
        </div>
    );
}

export default ({testParam}) => {

    return postUI(testParam);
}