import { Component } from "react";
import LineChart from "../Charts/line";
const env = require("../../env.json");

class TCExecutionPerDay extends Component {
    constructor(props) {
        super(props);
        this.state = {
            error: undefined,
            isLoaded: false,
            items: {}
        };
    }

    fetchGetTCExecutionPerDay() {
        var projectName = localStorage.getItem("projectPath");
        var uri = env.server + "dashboard/gettcexecutionperday?project_path=" + projectName;
        fetch(uri)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
        );
    }

    componentDidMount() {
        this.setState({
            items: undefined
        })
        this.fetchGetTCExecutionPerDay();
    }

    options(textData) {
        return {
          responsive: true,
          plugins: {
            legend: {
              position: 'top',
            },
            title: {
              display: true,
              text: textData,
            },
          }
        }
    };
      
    labels(dataSets) {
        return Object.keys(dataSets);
    }

    dataArr(dataSets) {
        var dataKeys = Object.keys(dataSets);
        var dataArr = [];
        for(let i = 0; i < dataKeys.length; i ++) {
          dataArr.push(dataSets[dataKeys[i]]);
        }
        return dataArr;
    }

    randomTill255() {
        return Math.floor(Math.random() * 200) + 55;
    }

    getRandomRGB() {
        return "rgba(" + this.randomTill255() + "," + this.randomTill255() + "," + this.randomTill255() + ")";
    }

    data(dataList) {
        var rgba = this.getRandomRGB();
        return {
            labels: this.labels(dataList),
            datasets: [
              {
                label: 'Execution count',
                data: this.dataArr(dataList),
                backgroundColor: rgba,
              }
            ],
        };
    };

    render() {
        const {error, isLoaded, items} = this.state;
        
        if(error) {
            return <div>error</div>
        } else if(!isLoaded) {
            return <div style={{width: "40%", position: "relative", left: "5%", marginTop: "5%", display: "inline-block"}}>
                Loading...
            </div>;
        } else {
            return  <div>
                        <div style={{width: "40%", position: "relative", left: "5%", marginTop: "5%", display: "inline-block"}}>
                            <LineChart  
                                options={this.options("Test Case Execution Per Day")}
                                data={this.data(items)}
                            />
                        </div>
                        <div style={{
                                    right: "4%",
                                    display: "inline-block", 
                                    position: "absolute",
                                    width: "30%", 
                                    marginTop: "6%",
                                    height: "35%",
                                    backgroundColor: "#d9def763",
                                    borderRadius: "10px"
                                }}>
                            <nav class="navbar navbar-default" style={{backgroundColor: "#5f40f5", borderTopLeftRadius: "10px", borderTopRightRadius: "10px"}}>
                                <div class="container-fluid">
                                    <div class="navbar-header" style={{color: "white"}}>Automation Execution Trends</div>
                                </div>
                            </nav>
                            <div style={{padding: "25px"}}>
                                Trend shows automation progress from the execution status point of view. 
                                Higher pass rate means more stable build.
                                The "Other" bar includes tests which are skipped.<br/>
                                Skipped test cases are not failed test cases, but not cosidered as part of batch.
                            </div>
                        </div>
                    </div>;
        }
    }
}

export default TCExecutionPerDay;