import { useState } from "react";
import {Alert, Button} from "react-bootstrap";

function DismissableAlert(showAlert, title, message, alertType) {
    const [show, setShow] = useState(showAlert);
  
    if (show) {
      if(message === undefined || message === "") {
        return (
          <Alert variant={alertType} onClose={() => setShow(false)} dismissible>
            {title}
          </Alert>
        );
      } else {
        return (
          <Alert variant={alertType} onClose={() => setShow(false)} dismissible>
            <Alert.Heading>{title}</Alert.Heading>
            <p>{message}</p>
          </Alert>
        );
      }
    }
    return <div style={{display: "none"}}></div>
}

export default ({showAlert, title, message, alertType}) => {
    return DismissableAlert(showAlert, title, message, alertType);
}