import {React} from 'react';
import ReactDOM from 'react-dom';
import ReactMarkdown from 'react-markdown'
const env = require("../env.json");

function fetchReadMeFile() {
    var projectName = localStorage.getItem("projectPath");
    var uri = env.server + "project/getgitreadme?project_path=" + projectName;
    fetch(uri)
        .then(res => res.json())
        .then(
            (result) => {
                ReactDOM.render(<ReactMarkdown>{result["data"]}</ReactMarkdown>, document.body)
            },
            (error) => {
                // this.setState({
                //     isReadMeFileLoaded: false,
                //     error
                // });
            }
        );
}

export default () => {
    return <div id="help"></div>
}