import React from 'react';
import ReactDOM from 'react-dom';

function environmentUI(envData) {
    return (
        <form style={{position: "relative", top: "5%"}}>
                <div class="form-group row" style={{marginRight: "0px"}}>
                    <label for="staticEmail" class="col-sm-3 col-form-label">Environment</label>
                    <div class="col-sm-6">
                        <select 
                            onChange={(e) => handleEnvChange(e.target.value, envData)} 
                            defaultValue={Object.keys(envData)[0]} readonly class="form-control-plaintext" id="envName">
                                { getEnvOptions(envData) }
                        </select>
                    </div>
                </div>
                <hr/>
                <div class="form-group row" style={{marginRight: "0px"}}>
                    <label class="col-sm-3 col-form-label">Details</label>
                </div>
                <ul class="list-group">
                    <li class="list-group-item" id="envDataList">
                        { getEnvValue(envData, Object.keys(envData)[0]) }
                    </li>
                </ul>
            </form>
    )
}

function getEnvOptions(envData) {
    var optionList = [];
    console.log(Object.keys(envData));
    for(let i = 0; i < Object.keys(envData).length; i ++) {
        optionList.push(<option value={Object.keys(envData)[i]}>{Object.keys(envData)[i]}</option>)
    }
    return optionList;
}

function getEnvValue(envData, envName) {
    envData = envData[envName];
    var envParams = [];
    for(let i = 0; i < Object.keys(envData).length; i ++) {
        envParams.push(
            <li class="list-group-item">
                <div style={{display: "inline-block", width: "35%"}}>{Object.keys(envData)[i]}</div>
                <div style={{display: "inline-block"}}>{envData[Object.keys(envData)[i]]}</div>
            </li>
        )
    }
    return envParams;
}

function handleEnvChange(envName, envData) {
    ReactDOM.render(
        getEnvValue(envData, envName),
        document.getElementById("envDataList")
    );
}

function frameworkConfigUI(configData) {
    var configVal = [];
    for(let i = 0; i < Object.keys(configData).length; i ++) {
        configVal.push(
            <li class="list-group-item">
                <div style={{display: "inline-block", width: "35%"}}>{Object.keys(configData)[i]}</div>
                <div style={{display: "inline-block"}}>{configData[Object.keys(configData)[i]]}</div>
            </li>
        )
    }
    return <div style={{position: "relative", width: "90%", top: "1%"}}>{configVal}</div>;
}

function testCaseListForSuite(configData) {
    var testList = [];
    for(let i = 0; i < Object.keys(configData).length; i ++) {
        testList.push(
            <tr>
                <th scope="row">{i + 1}</th>
                <td>{Object.keys(configData)[i]}</td>
                <td>
                    <select class="form-control-plaintext" defaultValue={configData[Object.keys(configData)[i]]["ToExecute"]}>
                        <option>Yes</option>
                        <option>No</option>
                    </select>
                </td>
                <td>
                    <select class="form-control-plaintext" defaultValue={configData[Object.keys(configData)[i]]["TestSource"]}>
                        <option>Scriptless</option>
                        <option>Coded</option>
                    </select>
                </td>
            </tr>
        )
    }
    return testList;
}

function testSuiteUI(configData) {
    return (
        <table class="table">
            <thead>
                <tr>
                <th scope="col">#</th>
                <th scope="col">Test case name</th>
                <th scope="col">To execute</th>
                <th scope="col">Test source</th>
                </tr>
            </thead>
            <tbody>
                { testCaseListForSuite(configData) }
            </tbody>
        </table>
    )
}

export default ({configData, configName}) => {
    if(configName === "Environment")
        return environmentUI(configData);
    else if(configName === "FrameworkConfig")
        return frameworkConfigUI(configData);
    else if(configName === "TestSuite")
        return testSuiteUI(configData);
}