import { Component } from "react";
import BarChart from "../Charts/bar";
const env = require("../../env.json");

class TCPassFailPerExecution extends Component {
    constructor(props) {
        super(props);
        this.state = {
            error: undefined,
            isLoaded: false,
            items: {}
        };
    }

    fetchGetTCExecutionPerDay() {
        var projectName = localStorage.getItem("projectPath");
        var uri = env.server + "dashboard/getpassfailcountperexecution?project_path=" + projectName;
        fetch(uri)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
        );
    }

    componentDidMount() {
        this.setState({
            items: undefined
        })
        this.fetchGetTCExecutionPerDay();
    }

    options(textData) {
        return {
          responsive: true,
          plugins: {
            legend: {
              position: 'top',
            },
            title: {
              display: true,
              text: textData,
            },
          }
        }
    };
      
    labels(dataList) {
        var labelList = [];
        for(let i = 0; i < dataList.length; i ++) {
            labelList.push(dataList[i]["DateStamp"]);
        }
        return labelList;
    }

    dataArr(dataSets) {
        var data = [];
        var pass = [], fail = [], other = [];
        dataSets.forEach(eachData => {
            pass.push(eachData["Result"]["Pass"]);
            fail.push(eachData["Result"]["Fail"]);
            other.push(eachData["Result"]["Other"]);
        });
        data.push({
            "label": "Pass",
            "data": pass,
            "backgroundColor": this.getRandomRGB(),
        })
        data.push({
            "label": "Fail",
            "data": fail,
            "backgroundColor": this.getRandomRGB(),
        })
        data.push({
            "label": "Skips",
            "data": other,
            "backgroundColor": this.getRandomRGB(),
        })
        return data;
    }

    randomTill255() {
        return Math.floor(Math.random() * 200) + 55;
    }

    getRandomRGB() {
        return "rgba(" + this.randomTill255() + "," + this.randomTill255() + "," + this.randomTill255() + ")";
    }

    data(dataList) {
        console.log(this.dataArr(dataList));
        return {
            labels: this.labels(dataList),
            datasets: this.dataArr(dataList)
        };
    };

    render() {
        const {error, isLoaded, items} = this.state;
        
        if(error) {
            return <div>error</div>
        } else if(!isLoaded) {
            return <div style={{width: "50%", position: "relative", left: "5%", marginTop: "2%", display: "inline-block"}}>
                Loading...
            </div>;
        } else {
            return  <>
                        <div style={{display: "inline-block", width: "50%", position: "relative", left: "45%", marginTop: "5%"}}>
                            <BarChart  
                                options={this.options("Test Case Status Per Execution")}
                                data={this.data(items)}
                            />
                        </div>
                        <div 
                            style={{
                                display: "inline-block", 
                                position: "absolute", 
                                left: "21%", 
                                width: "30%", 
                                marginTop: "6%",
                                height: "35%",
                                backgroundColor: "#d9def763",
                                borderRadius: "10px"
                            }}>
                            <nav class="navbar navbar-default" style={{backgroundColor: "#5f40f5", borderTopLeftRadius: "10px", borderTopRightRadius: "10px"}}>
                                <div class="container-fluid">
                                    <div class="navbar-header" style={{color: "white"}}>Test Case Status Per Execution</div>
                                </div>
                            </nav>
                            <div style={{padding: "25px"}}>
                                Trend shows execution status of the builds in TEST environment 
                                on the basis of date-time stamp in chronological order
                            </div>
                        </div>
                    </>;
        }
    }
}

export default TCPassFailPerExecution;