import {React, Component} from 'react';
import ReactDOM from 'react-dom';
import {ListGroup, Badge} from 'react-bootstrap';
require('../bootstrap/css/bootstrap.min.css');
const env = require('../env.json');

class Reports extends Component {
    fetchReportsList() {
        var uri = env.server + "reports/getallreports?project_path=" + localStorage.getItem("projectPath");
        fetch(uri)
            .then(res => res.json())
            .then(
                (result) => {
                    ReactDOM.render(
                        this.getAllReportsList(result),
                        document.getElementById("content-view")
                    )
                },
                (error) => {
                    console.log(error);
                }
        );
    }

    getDateTimeInFormat(rawDateTime) {
        var month = rawDateTime.split("_")[1];
        var date = rawDateTime.split("_")[2];
        var year = rawDateTime.split("_")[3];
        var hr = rawDateTime.split("_")[4];
        var min = rawDateTime.split("_")[5];
        var sec = rawDateTime.split("_")[6];
        return date + "/" + month + "/" + year + " " + hr + " : " + min + " : " + sec;
    }

    getAllNewLines(data) {
        var newLinedData = [];
        var splittedData = data.split("\n");
        for(let i = 0; i < splittedData.length; i ++) {
            newLinedData.push(
                <div style={{wordWrap: "break-word", fontFamily: "monospace", marginLeft: "15px"}}>{splittedData[i]}</div>
            )
        }
        return newLinedData;
    }

    getLogs(reportObject) {
        reportObject = reportObject.children
        var logPath = "";
        for(let i = 0; i < reportObject.length; i ++) {
            if(reportObject[i].name.includes("Logs-")) {
                logPath = reportObject[i].path;
                break;
            }
        }
        var uri = env.server + 'reports/logs?path=' + logPath;
        fetch(uri)
            .then(res => res.json())
            .then(
                (result) => {
                    ReactDOM.render(
                        this.getAllNewLines(result["logs"]),
                        document.getElementById("content-view")
                    )
                },
                (error) => {
                    console.log(error);
                }
            );
    }

    getReport(reportObject) {
        reportObject = reportObject.children
        var logPath = "";
        for(let i = 0; i < reportObject.length; i ++) {
            if(reportObject[i].name == "index.html") {
                logPath = reportObject[i].path;
                break;
            }
        }
        return logPath;
    }

    getAllReportsList(reportsList) {
        var reportsListView = [];
        for(let i = 0; i < reportsList.length; i ++) {
            reportsListView.push(
                <ListGroup.Item as="li" class="d-flex justify-content-between align-items-start hover-list-item">
                    <img src={require("../images/report.png")} style={{display: "inline-block", width: "5%"}}/>
                    <div className="ms-2 me-auto" style={{display: "inline-block", fontSize: "13px", position: "relative", left: "2%"}}>
                        <div style={{fontSize: "15px"}} className="fw-bold">{reportsList[i].name}</div>
                        {"Executed on " + this.getDateTimeInFormat(reportsList[i].name)}
                    </div>
                    <div className="ms-2 me-auto" style={{display: "inline-block", fontSize: "13px", position: "relative", left: "2%"}}>
                        {"Executed on " + this.getDateTimeInFormat(reportsList[i].name)}
                    </div>
                    <div style={{display: "block", width: "20%", float: "right", position: "relative"}} variant="primary" pill>
                        <span style={{cursor: "pointer", color: "#0d6efd"}} onClick={() => this.getLogs(reportsList[i])}>Logs</span>
                        <a href={env.server + "reports/report?path=" + this.getReport(reportsList[i])} 
                            style={{marginLeft: "60%", textDecoration: "none"}}>Report</a>
                    </div>
                </ListGroup.Item>
            )
        }
        return reportsListView;
    }

    render() {
        return (
            <ListGroup as="ol" numbered style={{paddingTop: "7px"}}>
                {this.fetchReportsList()}
            </ListGroup>
        );
    }
}

export default Reports;