import React from "react";
import ReactDOM from "react-dom";
import {Modal, Button} from 'react-bootstrap';
import TreeView from '@material-ui/lab/TreeView';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';
import SuccessMessage from './components/SuccessMessage';
import FrameworkConfigs from "./components/FrameworkConfigs";
const env = require("../../env.json");

var nodeId = 0;

export default ({handleClick, items}) => {
    const [modalShow, setModalShow] = React.useState(false);
    return (generateTreeView(items, handleClick, modalShow, setModalShow));
}

function handleHover(itemName) {
    var item = document.getElementById("img" + itemName).style.display;
    if(item == "none")
        document.getElementById("img" + itemName).style.display = "block";
    else
        document.getElementById("img" + itemName).style.display = "none";
}

export function getChild(childArr, itemName, handleClick) {
    var childs = [];
    if(childArr != undefined && childArr.length != 0) {
        childArr.forEach(item => {
            if(itemName == "project")
                childs.push(<TreeItem 
                    endIcon={<img style={{width: "100%"}} src={require('../../images/tc.png')} />}
                    // onMouseOver={() => document.getElementById("img" + item.name.split(".")[0]).style.display = "block" }
                    // onMouseOut={() => document.getElementById("img" + item.name.split(".")[0]).style.display = "none" }
                    nodeId={(++nodeId).toString()} onClick={(e) => handleClick(e) } 
                    label={
                        <div style={{display: "inline-flex"}}>
                        <div id={"label" + item.name.split(".")[0]}>{item.name.split(".")[0]}</div>
                        {/* <img id={"img" + item.name.split(".")[0]}
                            onClick={(e) => {
                                runTest(item.name.split(".")[0])}
                            } 
                            style={{display: "none", width: "13%", position: "absolute", left: "8em"}} 
                            src={require("../../images/run.png")} /> */}
                        </div>
                    }  key={nodeId}>
                </TreeItem>)
            else
                childs.push(<TreeItem 
                    endIcon={<img style={{width: "100%"}} src={require('../../images/config_logo.png')} />}
                    onClick={(e) => handleFrameworkDetails(e)} nodeId={(++nodeId).toString()} label={ item.name.split(".")[0] } />)
        });
    }
    return childs;
}

function handleFrameworkDetails(event) {
    var configName = event.target.innerText;
    var uri = env.server + "framework/" + configName + "?project_path=" + localStorage.getItem("projectPath");
    fetch(uri)
        .then(res => res.json())
        .then(
            (result) => {
                ReactDOM.render(<FrameworkConfigs configData={result} configName={configName}/>,
                    document.getElementById("content-view"));
            },
            (error) => {
                console.log(error)
            }
        );
}

function handleNewTestCaseName(event) {
    var name = event.target.value;
    var uri = env.server + "project/checktcexist?project_path=" + localStorage.getItem("projectPath") + "&test_name=" + name;
    fetch(uri)
        .then(res => res.json())
        .then(
            (result) => {
                console.log(result);
                if(result === true) {
                    document.getElementById("dupMessage").style.display = "block";
                } else {
                    document.getElementById("dupMessage").style.display = "none";
                }
            },
            (error) => {
                
            }
        );
}

function handleNewTestCaseSave(props) {
    var uri = env.server + "project/createtest";
    props.onHide();
    var saveBody = {
        "project_path": localStorage.getItem("projectPath"), 
        "test_name": document.getElementById("TestName").value,
        "to_execute": document.getElementById("ToExecute").value,
        "test_source": document.getElementById("TestSource").value
    }
    fetch(uri, {
        body: JSON.stringify(saveBody),
        headers: {
            "content-type": "application/json"
        },
        method: "put"
    }).then((result) => result.json())
    .then((result) => {
        ReactDOM.render(
            <SuccessMessage showAlert={true} title="Saved successfully" message="" alertType="success"/>,
            document.getElementById("message")
        );
    },(error) => {
        console.log(error);
    })
}

function AddNewTest(props) {
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Add new test case
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <div className="form-group">
                <div class="input-group" style={{width: "100%",
                    display: "-webkit-inline-box"}}>
                    <input style={{width: "inherit"}} onChange={(e) => handleNewTestCaseName(e)} id="TestName" type="text" class="form-control" placeholder="Test case name" />
                    <div id="dupMessage" style={{position: "relative", display: "none", left: "13px", color: "red", fontSize: "13px"}}>This test case already exists</div>
                </div>
            </div>
        </Modal.Body>
        <Modal.Footer style={{display: "block"}}>
            <div class="input-group" style={{width: "13%", display: "-webkit-inline-box"}}>
                <select style={{width: "100%"}} className="form-control" name="ToExecute" id="ToExecute">
                    <option defaultValue={undefined}>To Execute</option>
                    <option value="Yes">Yes</option>
                    <option value="No">No</option>
                </select>
            </div>
            <div class="input-group" style={{width: "14%", display: "-webkit-inline-box"}}>
                <select style={{width: "100%"}} className="form-control" name="TestSource" id="TestSource">
                    <option defaultValue>Test Source</option>
                    <option value="Scriptless">Scriptless</option>
                    <option value="Coded">Coded</option>
                </select>
            </div>
            <Button id="save" onClick={() => handleNewTestCaseSave(props)} style={{float: "right"}}>Save</Button>
        </Modal.Footer>
      </Modal>
    );
}

export function generateTreeView(items, handleClick, modalShow, setModalShow, refreshFolderStructure) {
    return (<TreeView defaultCollapseIcon={<ExpandMoreIcon />} defaultExpandIcon={<ChevronRightIcon />} >
        <AddNewTest
          show={modalShow}
          onHide={() => setModalShow(false)}
        />
        {
            Object.keys(items).map((item) => (
                <TreeItem
                    expandIcon={<img style={{width: "100%"}} src={require('../../images/collapsed_folder.png')} />}
                    collapseIcon={<img style={{width: "100%"}} src={require('../../images/expand_folder.jpg')} />}
                    nodeId={(++nodeId).toString()}
                    onMouseOver={() => {
                        if(document.getElementById("label" + item).innerText != "framework")
                            document.getElementById("img" + item).style.display = "block"} 
                    }
                    onMouseOut={() => {
                        if(document.getElementById("label" + item).innerText != "framework")
                            document.getElementById("img" + item).style.display = "none" }
                    }
                    label={
                    <div style={{display: "inline-flex"}}>
                        <div id={"label" + item}>{item.split(".")[0]}</div>
                        <img onClick={() => setModalShow(true) } id={"img" + item} 
                            style={{display: "none", width: "13%", position: "absolute", left: "9em"}} 
                            src={require("../../images/add.png")} />
                    </div>
                }>
                    { getChild(items[item], item, handleClick) }
                </TreeItem>
            ))
        }
    </TreeView>)
}