import { Component } from "react";
import LineChart from "../Charts/line";
const env = require("../../env.json");

class ExecutionTimePerTC extends Component {
    constructor(props) {
        super(props);
        this.state = {
            error: undefined,
            isLoaded: false,
            items: []
        };
    }

    fetchExecutionTimePerTC() {
        var projectName = localStorage.getItem("projectPath");
        var uri = env.server + "dashboard/getexecutiontimepertc?project_path=" + projectName;
        fetch(uri)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
        );
    }

    componentDidMount() {
        this.setState({
            items: undefined
        })
        this.fetchExecutionTimePerTC();
    }

    options(textData) {
        return {
          responsive: true,
          plugins: {
            legend: {
              position: 'top',
            },
            title: {
              display: true,
              text: textData,
            }
          }
        }
    };
      
    labels(dataList) {
        var labelList = [];
        for(let i = 0; i < dataList.length; i ++) {
            labelList.push("Run " + (i + 1));
        }
        return labelList;
    }

    dataArr(dataSets) {
        var obj = [];
        dataSets.forEach(eachSet => {
            var rgba = this.getRandomRGB();
            obj.push({
                "label": eachSet["TestName"],
                "data": eachSet["ExecutionTime"],
                "backgroundColor": rgba
            });
        });
        return obj;
    }

    randomTill255() {
        return Math.floor(Math.random() * 200) + 55;
    }

    getRandomRGB() {
        return "rgba(" + this.randomTill255() + "," + this.randomTill255() + "," + this.randomTill255() + ")";
    }

    data(dataList) {
        return {
            labels: this.labels(dataList),
            datasets: this.dataArr(dataList)
        };
    };

    render() {
        const {error, isLoaded, items} = this.state;
        
        if(error) {
            return <div>error</div>
        } else if(!isLoaded) {
            return <div style={{width: "40%", position: "relative", left: "15%", marginTop: "2%", display: "inline-block"}}>
                Loading...
            </div>;
        } else {
            return <>
                    <div style={{width: "50%", position: "relative", left: "45%", marginTop: "5%", display: "inline-block"}}>
                        <LineChart  
                            options={this.options("Execution Time For Each Test Case")}
                            data={this.data(items)}
                        />
                    </div>
                    <div style={{
                            display: "inline-block", 
                            position: "absolute",
                            left: "21%", 
                            width: "30%", 
                            marginTop: "6%",
                            height: "35%",
                            backgroundColor: "#d9def763",
                            borderRadius: "10px"
                        }}>
                        <nav class="navbar navbar-default" style={{backgroundColor: "#5f40f5", borderTopLeftRadius: "10px", borderTopRightRadius: "10px"}}>
                                <div class="container-fluid">
                                    <div class="navbar-header" style={{color: "white"}}>Automation Execution Trends</div>
                                </div>
                        </nav>
                        <div style={{padding: "25px"}}>
                            Trend shows total test case executed per execution. Server based executions are reported here. 
                            This trend is useful while tracking the automation progress on execution side. 
                            Also, this metrics talsk about the test automation development progress.
                        </div>
                    </div>
                </>;
        }
    }
}

export default ExecutionTimePerTC;