import React from 'react';
import ReactDOM from 'react-dom';
import '../css/index.css';
import { Component } from 'react';
import ContentView from './js/ContentView';
import FolderStructure from './js/FolderStructure';
import Header from './js/components/Header';
import "../css/App.css";
import Dashboard from '../Dashboard';
const env = require('../env.json');

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,
          items: {}
        };
    }

    loadFolderStructure() {
      var uri = env.server + 'project/getprojectdata?project_path=aura-api-automation';
        fetch(uri)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result
                    });
                    console.log(result);
                    localStorage.setItem("projectPath", "aura-api-automation");
                },
                (error) => {
                    this.setState({
                        isLoaded: false,
                        error
                    });
                }
            );
    }

    componentDidMount() {
        this.loadFolderStructure();
    }

    handleClick(event) {
      localStorage.setItem("curPage", "Home");
      localStorage.setItem("testName", event.target.innerText);
      ReactDOM.render(
        <></>,
        document.getElementById("content-view")
      );
      ReactDOM.render(
        <ContentView testCaseName={event.target.innerText} isStepsDisplayed={true}/>,
        document.getElementById("content-view")
      );
    }

    render() {
      const {error, isLoaded, items} = this.state;
      if(error) {
          return <div>{console.trace()}</div>
      } else if(!isLoaded) {
          return <div>Loading...</div>;
      } else {
        return(
          <div>
            <div id="header">
              <Header />
            </div>
            <div id="content-window">
              <table style={{width: "100%", tableLayout: "fixed", minHeight: "85vh"}}>
                <tbody>
                  <tr>
                    <td id="folder-structure" 
                      style={{verticalAlign: "top", width: "12em", overflow: "auto", backgroundColor: "#d9d2fb"}}>
                      { <FolderStructure handleClick={this.handleClick} items={this.state.items} /> }
                    </td>
                    <td id="content-view" style={{verticalAlign: "top"}}>
                      { <ContentView/> }
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        );
      }
    }
}

ReactDOM.render(<Home/>,
  document.getElementById("root"));

ReactDOM.render(
    <ContentView/>,
    document.getElementById("content-view")
);

if(window.location.href.includes("/Reports")) {
  ReactDOM.render(
    <Dashboard/>,
    document.getElementById("content-window")
  );
}

export default Home;