import { Component, React } from "react";
import BarChart from "./Charts/bar";
import NumberBox from "./Charts/NumberBox";
import ExecutionTimePerTC from "./Metrics/ExecutionTimePerTC";
import TCExecutionPerDay from "./Metrics/TCExecutionPerDay";
import TCExecutionPerSuite from "./Metrics/TCExecutionPerSuite";
import TCPassFailPerExecution from "./Metrics/TCPassFailPerExecution";

class Dashboard extends Component {

    render() {
        
        return <div>
                <div id="numberDisplay">
                    <nav class="navbar navbar-default" style={{backgroundColor: "#d9d2fb"}}>
                        <div class="container-fluid">
                            <div class="navbar-header" style={{color: "#010028"}}>Automation Numbers</div>
                        </div>
                    </nav>
                    <NumberBox />
                </div>
                <div id="executionTrends">
                    <nav class="navbar navbar-default" style={{backgroundColor: "#d9d2fb"}}>
                        <div class="container-fluid">
                            <div class="navbar-header" style={{color: "#010028"}}>Automation Execution Trends</div>
                        </div>
                    </nav>
                    <div>
                        <TCPassFailPerExecution />
                        {/* <div style={{width: "40%", position: "relative", left: "15%", marginTop: "2%", display: "inline-block"}}>
                            <BarChart text="Total Test Cases vs Failed Cases" />
                        </div> */}
                    </div>
                </div>
                <div id="dailyreport">
                    <nav class="navbar navbar-default" style={{backgroundColor: "#d9d2fb"}}>
                        <div class="container-fluid">
                            <div class="navbar-header" style={{color: "#010028"}}>Daily Status Reports</div>
                        </div>
                    </nav>
                    <div>
                        <TCExecutionPerSuite />
                    </div>
                    <div>
                        <TCExecutionPerDay/>
                    </div>
                    <div>
                        <ExecutionTimePerTC />
                    </div>
                </div>
            </div>;
    }
}

export default Dashboard;